import styles from "./Rebloom.module.css";
import img1 from "./-1.png";
import img2 from "./.png";
import img3 from "./-2.png";
import img4 from "./-3.png";
import img5 from "./-4.png";
import img6 from "./-5.png";
import img7 from "./-6.png";
import img8 from "./-7.png";
import img9 from "./-8.png";

const Rebloom = () => {
  const imgList = [img1, img2, img3, img4, img5, img6, img7, img8, img9];

  return (
    <div className={styles.imgField}>
      {imgList.map((img, index) => (
        <img key={index} className={styles.img} src={img} alt="img" />
      ))}
    </div>
  );
};

export default Rebloom;
