import { useParams } from "react-router-dom";
import styles from "./Project.module.css";

import { projects } from "../../data";

const ProjectDetail = () => {
  const { id } = useParams();
  const project = projects.find((p) => p.id === Number(id));

  if (!project) return <p>   .</p>;

  return (
    <div className={styles.project}>
      <img className={styles.mainImg} src={project.mainImg} alt="" />
      <div className={styles.textField}>
        <div className={styles.titleField}>
          <div className={styles.subtitle}>{project.subtitle}</div>
          <div className={styles.title}>{project.title}</div>
        </div>
        <div className={styles.subField}>
          <div className={styles.period}>
            <p className={styles.label}>
              <strong>Period</strong>
            </p>
            <p className={styles.text}>{project.period}</p>
          </div>
          <div className={styles.contribution}>
            <p className={styles.label}>
              <strong>Contribution</strong>
            </p>
            <p className={styles.text}>{project.contribution}</p>
          </div>
        </div>
      </div>
      <div className={styles.detail}>{project.detail}</div>
    </div>
  );
};

export default ProjectDetail;
